
import { Button, Modal } from "antd";
import React, { useEffect, useState } from "react";
import QuestionType, { defaultQuestion } from "../model/QuestionType";


type DetailPageProps = {
    question: QuestionType;
    isModalVisible: boolean;
    onOk: ()=> void;
}
export default function DetailPage(props: DetailPageProps) {

    const [question, setQuestion] = useState<QuestionType>(defaultQuestion);

    const footer = [
        <Button key="ok" type="primary" onClick={props.onOk}>
          OK
        </Button>,
    ]
    
    useEffect(() => {
        setQuestion(props.question);
     }, [props.question, props.isModalVisible]);
    

    return (
        <Modal visible={props.isModalVisible} closable centered title="en savoir plus ..." footer={footer} onOk={props.onOk}>
            <div className="knowMore">
                <div className="sentence-quantity">
                    <div className="sentence">{question.sentence}</div>
                    <div className="quantity">{question.quantity} steaks</div>
                </div>
                <p className="title">Hypothèses</p>
                <p className="text">{question.hypothesis}</p>        
                <p className="title">Alternatives</p>        
                <p className="text">{question.alternatives}</p>
            </div>        
        </Modal> 
    );
}