
import React, { useContext, useState } from "react";
import { useHistory } from "react-router-dom";
import Footer from "../Footer";
import GlobalContext from "../GlobalContext";
import Header from "../Header";
import CategoryType from "../model/Category";
import MyContextType from "../model/MyContextType";
import DetailPage from "./DetailPage";
import { Button, Modal } from "antd";


type CategoryProps = {
    category : CategoryType;
}


export default function Questions(props: CategoryProps) {
    const history = useHistory();
    const myContext:MyContextType = useContext(GlobalContext);
    const [isModalVisible, setIsModalVisible] =  useState<boolean>(false);
    const [questionIndex, setQuestionIndex] = useState<number>(0);

    function onClick(i: number) {
        console.log("onClick i=", i);
        console.log("myContext.questionList[i]) = ", myContext.questionList[i]);

        setQuestionIndex(i);
        setIsModalVisible(true);
    }

    function onCancel() {
        setIsModalVisible(false);
    }


    return (
        <React.Fragment>
            <Header back onClick={() => {history.goBack()}}/>
            <div className="body">
                <h1 >{myContext.currentCategory}</h1>
                <div>
                    { myContext.questionList
                            .filter(q => q.category === myContext.currentCategory)
                            .map ((question, i) => {
                                return (
                                    <div key={i} className="question" >
                                        <div className="sentence">{question.sentence}</div>
                                        <div className="quantity">{question.quantity} steaks</div>
                                        <Button className="knowMoreButton" type="primary" onClick={() => onClick(i)}>...</Button>
                                    </div>
                                )
                            })}
                </div>
                <DetailPage question={myContext.questionList[questionIndex]} isModalVisible={isModalVisible} onOk={onCancel}/>
            </div>
           <Footer/>
       </React.Fragment>
    )
} 