
import { Button } from "antd"
import QuestionType from "../model/QuestionType"

type CategoryButtonProps = {
    category: string;
    question: QuestionType;
    onClick: any;
    key: number;
}

export default function CategoryButton (props: CategoryButtonProps) {
    return (
        <div className="category">
            <Button className="categoryButton" type='primary' onClick={props.onClick} >
                {props.category}
            </Button>
            <div className="categorySentence">
                {props.question.sentence}
            </div> 
            <div className="categoryQuantity">
                {props.question.quantity} steaks
            </div>   
        </div>
    )
}